// custom styles for this project only
body {
  width: 100%;
  height: 100%;
  font-family: $font-primary;
  font-size: $font-size;
  line-height: $line-height;
}
.super-container {
  padding: 5px 0 0;
  @media (min-width: $break-mid) {
    padding: 20px 0 0;
    margin: 0 20px;
  }
}
header, footer, nav, .main {
  position: relative;
  @include flexbox;
}

header, footer {
  @include flex-flow(row wrap);
}
header {
  @include justify-content(space-between);
  @include align-items(flex-end);
  padding: 10px 0 10px 10px; // create space for contents to bleed over edge
  margin: -10px 0 -10px -10px; // visually align apparent edges
  overflow-y: visible;
  z-index: 500;
}
header > * {
  // @include devBorder(pink);
  & > * {
     // @include devBorder(green);
   & > * {
   // @include devBorder(gray);
   }
  }
}
footer {
  @include justify-content(center);
  @include align-items(flex-start);
  margin: 0 0 40px;
  padding: 0 10px;
  border-top: $border-primary;
  @media (min-width: $break-small) {
    padding: 0;
  }
  @media (min-width: $break-large) {
    @include align-items(center);
    padding-top: 10px;
  }
  & > div {
    @include flex(0,0,auto);
    width: 100%;
    margin: 20px auto 0;
    text-align: center;
  &:first-child {
    margin-top: 0;
  }
  @media (min-width: $break-small) {
    @include flex(0,1,auto);
    width: auto;
    }
  }
}
nav {
  @include flex(1, 1, auto);
  @include justify-content(space-between);
  @include align-items(flex-start);
  @include flex-wrap (wrap);
  @include order(10);
  padding: 0;
  margin: 0;
  border-bottom: $border-primary;
  @media (min-width: $break-small) {
    @include flex(1, 1, auto);
    margin: auto 0 0 auto;
    vertical-align: bottom;
    border-bottom: 0;
  }
  @media (min-width: $break-large) {
    @include order();
    @include flex(0, 1, auto);
    @include justify-content(flex-start);
    @include align-items(flex-end);
    margin: auto auto 0;
  }
}
a.nav-item {
  @include flex(0, 1, 100%);
  margin: 0 0 0 auto;
  padding: 10px 5px;
  text-transform: uppercase;
  text-align: center;
  @media (min-width: $break-small) {
    @include flex(1, 1, auto);
    margin: 0;
  }
  @media (min-width: $break-large) {
    @include flex(0, 1, auto);
    margin: 0 0 0 20px;
    padding: 5px 0;
    &:first-child {
      margin-left: 0;
    }
  }
}

.container-headerLogo {
  @include flex(0, 1, 45%);
  width: auto;
  margin: 0;
  z-index: 1000;
  @media (min-width: $break-small) {
    @include flex(0, 1, auto);
    margin: 0;
  }
  @media (min-width: $break-mid) {
    margin-left: -10px;
  }
  & a {
    display: block;
    width: auto;
    max-height: 50px;
    margin: 0 0 -5px 0;
  }
  & a img.logo-bm {
    margin: 0;
    max-height: 50px;
    width: auto;
  }
}

a.container-blogLink {
  @include flex(0, 1, 45%);
  margin: 0 5px 0 auto;
  padding-bottom: 2px;
  text-align: right;
  & img {
    height: $font-size * 1.2;
  }
  @media (min-width: $break-small) {
    @include flex(0, 1, auto);
    padding: 0;
  & img {
      height: $font-size * 1.5;
    }
  }
  @media (min-width: $break-mid) {
    margin: 0;
  }
}

div.main {
  margin: 20px 0;
}
section {
  width: 100%;
}

.container-image {
  a.image-link,
  a.image-link > img {
    display: block;
    width: 100%;
    margin-bottom: 0;
  }
}

.footerBusinessInfo {
  padding: 10px;
  line-height: $line-height * 1.33;
  & span.text {
    display: block;
    width: 100%;
    white-space: nowrap;
  }
  & span.dividerLine {
    display: none;
  }
  @media (min-width: $break-small) {
    & span.text,
    & span.dividerLine {
      display: inline;
    }
  }
  @media (min-width: $break-large) {
    margin: 0 auto;
    line-height: $line-height;
    padding: 0;
  }
}
.copyright,
.container-footerLogo {
  width: 100%;
  text-align: center;
}

.container-footerLogo {
  @include flexbox;
  @include justify-content(center);
  @include align-items(center);
}
.footerLogo {
  margin: 0 10px;
  & img {
  display: block;
  width: 5%;
  min-width: 20px;
  max-width: 35px;
  margin: 0 auto;
  padding: 2px 2px 0 0;
  }
}

.line {
  width: 100%;
  height: 1px;
  background-color: $color-primary;
  header & {
    @include flex(0,0,auto);
    margin-top: 5px;
    @media (min-width: $break-small) {
    margin: 0;
    }
  }
  footer & {
    width: 100px;
    opacity: .25;
  }
}

.content.error-page {
  @include justify-content(center);
  @include align-items(center);
  height: 50%;
  min-height: calc(40vh);
  & section.error-message {
    @include flex(0, 1, auto);
    width: auto;
    margin: 40px auto 80px;
    text-align: center;
  }
}